import React, { useEffect, useRef } from "react";
import gsap from "gsap";
import { Back } from "gsap/gsap-core";

export default function Logo({ visibility }) {
  const wrapperLogo = useRef(null);
  const mainTl = gsap.timeline().pause();
  useEffect(() => {
    const children = wrapperLogo.current.children;
    const ch1TL = gsap.timeline().pause();
    ch1TL
      .set(children[0], { y: "-50%", x: "-50%", duration:0} , '-=1')
      .to(children[0], {
        opacity: 1,
        scale: 1,
        ease: Back.easeOut.config(1.7),
        duration: 2,
      });
    if (visibility.logo === "visible") {
      mainTl
        .to(wrapperLogo.current, { autoAlpha: 1, top: "50%", zIndex:99 },'0')
        .play().add(ch1TL.play());
    }
  }, [visibility.logo]);
  return (
    <div className="Logo" ref={wrapperLogo}>
      <div className="logoImg" alt="logo"></div>
    </div>
  );
}
