import React, { useRef, useEffect } from "react";
import gsap from "gsap";
import placeOnCircle from '../../services/gsap/gsapService'

export default function ThirdNav({ clickHandler }) {
  const wrapper = useRef(null);

  useEffect(() => {
    const tl = gsap.timeline();
    placeOnCircle(wrapper.current.children, tl, -90, 180);
  }, []);
  return (
    <div ref={wrapper} className="ThirdNav">
      <div className="links-wrapper">
        <button
          onClick={() =>
            clickHandler(<i className="fas fa-smile-wink">Facebook</i>)
          }
        >
          <span>
          <i className="fab fa-facebook-f"></i>
          </span>
        </button>
        <button
          onClick={() =>
            clickHandler(
              <i className="fas fa-smile-wink">Informacje</i>
            )
          }
        >
          <span><i className="far fa-file-alt"></i></span>
        </button>
        <button
          onClick={() =>
            clickHandler(<i className="fas fa-smile-wink">Główna</i>)
          }
        >
          <span><i className="fas fa-home"></i></span>
        </button>
        <button
          onClick={() =>
            clickHandler(
              <i className="fas fa-smile-wink">Konto uzytkownika</i>
            )
          }
        >
          <span><i className="fas fa-user-circle"></i></span>
        </button>
        <button
          onClick={() =>
            clickHandler(<i className="fas fa-smile-wink">Instagram</i>)
          }
        >
          <span>
            <i className="fab fa-instagram"></i>
          </span>
        </button>
      </div>
    </div>
  );
}
