import React, { useEffect, useRef, useState } from "react";
import gsap from "gsap";
import { Power4 } from "gsap/gsap-core";
import Logo from "./components/Logo";
import Navigation from "./components/Navigation";
import ContentWrapper from "./components/ContentWrapper";
import VideoBg from "./components/VideoBg"

export default function App() {
  const wrapper = useRef(null);
  const mainTl = gsap.timeline().pause();
  //hidden | visible | rootOnly
  const [visibility, setVisibility] = useState({
    logo: "hidden",
    content: "hidden",
  });
  const [navProgress, setNavProgress] = useState({
    phase1: false,
    phase2: false,
  });
  const [mainContent, setContent] = useState("Hello");
  function navigationClickHandler(content) {
    setContent(content);
  }
  useEffect(() => {
    const [logo, nav, content] = wrapper.current.children;
    mainTl
      .set(wrapper.current, { display: "block" })
      .play()
      .add(setVisibility((c) => ({ ...c, logo: "visible" })))
      .add(setNavProgress((c) => ({ ...c, phase1: true })))
      .to(logo, { top: "80%", zIndex: 3, duration: .5, delay: 4, width:'20%',rotate:'90deg'})
      .to(logo, { top: "80%", left:'95%', duration: .5},'-=.5')
      .to(logo.children[0], { opacity: 0.4 }, "-=1")
      .to(nav, { top: "0", duration: 1 }, "-=1")
      .to(wrapper.current, { display: "grid", duration: 1 }, "+=2")
      .add(setNavProgress((c) => ({ ...c, phase2: true })))
      .to(
        content,
        {
          autoAlpha: 1,
          height: "100vh",
          duration: .5,
          transformOrigin: "0 50%",
          ease: Power4.easeIn,
        },
        "-=1.4"
      );
  }, []);
  return (
    <div className="App" ref={wrapper}>
      <Logo visibility={visibility} />
      <Navigation
        progress={navProgress}
        clickHandler={navigationClickHandler}
      />
      <ContentWrapper visibility={visibility} content={mainContent} />

    </div>
  );
}

