


export default function placeOnCircle(wrapper, tl, offset, scope) {
  const links = wrapper[0].children;
  tl.set(wrapper, { autoAlpha: 0 });
  let perStep = -(scope / (links.length - 1));
  for (let i = 0; i < links.length; i++) {
    const element = links[i];
    tl.set(element, {
      rotate: offset + perStep * i + "deg",
    });
    for (let index = 0; index < element.children.length; index++) {
      const child = element.children[index];
      tl.set(child, {
        rotate: -(offset + perStep * i) * Math.pow(index + 1, index) + "deg",
      });
      if ((i + 1) % 2 === 0) tl.to(child, { rotate: "180deg" });
    }
  }
}
