import React, { useRef, useEffect } from "react";
import gsap from "gsap";
import Color from "color";
import MainTabs from "./Navigation/MainTabs";
import SecondNav from "./Navigation/SecondNav";
import ThirdNav from "./Navigation/ThirdNav";
import BasketNav from "./Navigation/BasketNav";

export default function Navigation({ clickHandler, progress }) {
  const wrapper = useRef(null);
  const mainTl = gsap.timeline().delay(2).pause();
  useEffect(() => {
    const children = wrapper.current.children;
    if (progress.phase1) {
      mainTl
        .play()
        .set(wrapper.current, { top: "50%", y: "-50%", autoAlpha: 0 })
        .set([children[3], children[2], children[1]], {
          top: "50%",
          left: "50%",
          y: "-50%",
          x: "-50%",
          scale: 0,
        })
        .set(children[1], {
          background: Color.rgb(215, 226, 188).alpha(0.3).darken(0.2),
          backdropFilter: "blur(2px)",
          boxShadow: "0px 1px 2px rgba(0, 0, 0, 0.2)",
        })
        .set(children[0], {
          background: Color.rgb(215, 226, 188).alpha(0.3).darken(0.1),
          backdropFilter: "blur(2px)",
          boxShadow: "0px 1px 2px rgba(0, 0, 0, 0.2)",
          width: "50rem",
          height: "50rem",
          borderRadius: "50%",
          scale: 0,
        })
        .pause()
        .set(wrapper.current, { autoAlpha: 1 })
        .play()
        .to([children[3], children[2], children[1], children[0]], {
          duration: 1,
          scale: 1,
          stagger: ".12",
        })
        .to([children[0], children[1], children[2]], {
          duration: 0.8,
          scale: 0,
          stagger: ".12",
        });
    }
  }, [progress.phase1]);
  useEffect(() => {
    const children = wrapper.current.children;

    if (progress.phase2) {
      console.log(children[2].children[0]);
      mainTl
        .to(children[3], { y: "-25%", duration: 1 }, "+=.6")
        .set(children[2], { y: "-35%" })
        .set(children[1], {
          y: "-40%",
          background: "none",
          backdropFilter: "none",
          boxShadow: "none",
        })
        .set(children[0], {
          width: "75%",
          height: "7rem",
          borderRadius: "0 0 5px 5px",
          y: "50%",
        })
        .to(children[3].children[0], { autoAlpha: 1 })
        .set(
          [
            children[2].children[0],
            children[1].children[0],
            children[0].children[0],
            children[0].children[1],
          ],
          {
            autoAlpha: 1,
            width: "100%",
            height: "100%",
            transformOrigin: " center",
          },
          "-=1"
        )
        .to(
          [children[2], children[1], children[0]],
          { scale: 1, duration: 1 },
          "-=.4"
        );
    }
  }, [progress.phase2]);
  return (
    <div className="Navigation" ref={wrapper}>
      <MainTabs clickHandler={clickHandler} />
      <SecondNav clickHandler={clickHandler} />
      <ThirdNav clickHandler={clickHandler} />
      <BasketNav clickHandler={clickHandler} />
    </div>
  );
}
