import React from "react";
import ProductsList from "../Content/ProductsList";

export default function MainTabs({ clickHandler }) {
  return (
    <div className="MainTabs">
      <button
        onClick={() =>
          clickHandler(<i className="fas fa-smile-wink"><ProductsList /></i>)
        }
      >
        DLA NIEJ
      </button>
      <button
        onClick={() =>
          clickHandler(<i className="fas fa-smile-wink"><ProductsList /></i>)
        }
      >
        DLA NIEGO
      </button>
    </div>
  );
}
