import React, { useRef, useEffect } from "react";
import placeOnCircle from '../../services/gsap/gsapService'
import gsap from "gsap";

export default function SecondNav({ clickHandler }) {
  const wrapper = useRef(null);

  useEffect(() => {
      const tl = gsap.timeline();
      placeOnCircle(wrapper.current.children, tl, -90, 180);
  }, []);

  return (
    <div ref={wrapper} className="SecondNav">
      <div className="links-wrapper">
        <button
          onClick={() =>
            clickHandler(<i className="fas fa-smile-wink">Ustawienia</i>)
          }
        >
          <span>
            <i className="fas fa-cog"></i>
          </span>
        </button>
        <button
          onClick={() =>
            clickHandler(
              <i className="fas fa-smile-wink">Produkty second hand</i>
            )
          }
        >
          <span><span>Second Hand</span></span>
        </button>
        <button
          onClick={() =>
            clickHandler(<i className="fas fa-smile-wink">Produkty Outlet</i>)
          }
        >
          <span><span>Outlet</span></span>
        </button>
        <button
          onClick={() =>
            clickHandler(
              <i className="fas fa-smile-wink">Produkty dla Vintage</i>
            )
          }
        >
          <span><span>Vintage</span></span>
        </button>
        <button
          onClick={() =>
            clickHandler(<i className="fas fa-smile-wink">Log-In</i>)
          }
        >
          <span>
            <i className="fas fa-sign-in-alt"></i>
          </span>
        </button>
      </div>
    </div>
  );
}
